.navbar__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar__container-logo {
    height: 75px;
    width: 95px;
    padding: 0px 0px 0 20px;
}
.navbar-title {
    font-family: "Ruda", sans-serif;
    font-weight: 800;
    font-size: 1.7em;
}
.navbar-title h1 {
    margin: 10px 0;
    padding-left: 150px;
}
.menu-list__container ul {
    display: flex;
    list-style: none;
}
.menu-list__container button {
    border: none;
    background-color: #fff;
    font-family: "Ruda", sans-serif;
    font-weight: 700;
    font-size: 0.9em;
    text-align: center;
    align-items: center;
    height: 30px;
    width: 60px;
    margin-left: 20px;
}
button:focus {
    outline: none;
}

.menu-list__container button:hover {
    background-color: #0d7377;
    color: #fff;
    font-weight: 800;
    display: flex;
    justify-content: center;
    border-radius: 25px;
}
.nav-links {
    text-decoration: none;
    align-items: center;
    font-size: 16px;
    height: 20px;
    width: 60px;
    color: #212121;
}
.nav-links:hover {
    background-color: #0d7377;
    color: #fff;
}
.menu-list__container li {
    padding: 0px 5px;
}
